
.ui-animate .uicore-base{max-height:95vh;width:295px;height:auto;bottom:auto;top:30vh
}
.ui-animate .uicore-base_title{cursor:grab;font-size:13px;font-weight:600
}
.ui-animate .uianim-check{position:absolute;top:0;left:0;border-radius:3px;height:15px;width:15px;background:#fff;border:1px solid #cbd5e1
}
.ui-animate .uianim-check:after{content:"";position:absolute;display:none;left:5.5px;top:2px;width:3px;height:8px;border:solid #fff;border-width:0 2px 2px 0;transform:rotate(45deg)
}
.ui-animate .uianim-actions{padding:10px 20px 20px;display:flex;gap:5px
}
.ui-animate ul{min-height:10px;padding:20px 20px 7px
}
.ui-animate .uianim-btn{margin-top:12px;padding:8px;width:40%;text-align:center;background:var(--uicore-primary,#532df5);color:#fff;border-radius:4px;display:inline-block;cursor:pointer;transition:background .3s ease-in-out,color .3s ease-in-out;will-change:background,color
}
.ui-animate .uianim-btn:hover{background:#1b191e
}
.ui-animate .uianim-btn.uianim-second{background:#f1f5f9;color:#0f172a;width:60%
}
.ui-animate .uianim-btn.uianim-second:hover{background:#e5e7eb;color:#000
}
.ui-animate li{margin-bottom:2px;padding:4px 6px!important;background:#f1f5f9;border-radius:3px;border:2px solid transparent;transition:all .2s ease-in-out;cursor:grab;display:flex!important
}
.ui-animate li .uianim-widget{flex:1
}
.ui-animate li svg{max-width:13px;max-height:13px;vertical-align:text-top;opacity:.6;cursor:pointer
}
.ui-animate li svg:hover{opacity:1
}
.ui-animate li:hover{border:2px solid var(--uicore-primary,#532df5)
}
.ui-animate li.uianim-is-active{border:2px solid rgba(27,25,30,.08);background:#f1f5f9
}
.ui-animate .uianim-content__empty__title{font-size:13px;font-weight:500;text-align:center;padding:35px 20px;display:flex;flex-direction:column;align-items:center;gap:18px;line-height:1.5;opacity:.6
}
.ui-animate .uianim-remove{color:#c1320b;line-height:1.9;cursor:pointer;opacity:.7;transition:opacity .2s ease-in-out;will-change:opacity
}
.ui-animate .uianim-remove:hover{opacity:1
}
.ui-animate .uicore-base_title svg{cursor:grab!important
}
.uianim-start{position:fixed;top:25vh;right:0;background:#fff;border:1px solid #e5e7eb;border-radius:6px 0 0 6px;padding:10px;z-index:9999;box-shadow:0 4px 4px hsla(0,0%,68.2%,.25),0 9px 40px -15px hsla(0,0%,68.2%,.25)
}
.uianim-start svg{transform:translateY(1px)
}
.uianim-start svg path{transition:all .3s ease
}
.uianim-start svg path:first-child{transform:translateY(-3px);opacity:0
}
.uianim-start svg path:nth-child(2){transform:translateY(-1px);opacity:0
}
.uianim-start svg path:nth-child(3){transform:translateY(4px)
}
.uianim-start:hover{cursor:pointer
}
.uianim-start:hover path{transform:translateY(0)!important;opacity:1!important
}
.uicore-base{font-family:Inter,sans-serif;--uicore-color-9:#e7eaef;--uicore-color-8:#172b4d;--uicore-color-7:#1c2c4e;--uicore-color-6:#1c2c4e;--uicore-color-5:#5f6875;--uicore-color-4:#b0b8ca;--uicore-color-3:#eef0f5;--uicore-color-2:#fff;--uicore-color-1:#f4f5f7;--uicore-base-border-color:#f1f1f3;--uicore-base-input-bg:#f7f7f7;--uicore-base-input-focus-bg:#eef0f5;--uicore-base-text-strong:#2f2f32;--uicore-base-text:#5f5f5f;--uicore-default-input-width:170px;--uicore-xs-input-width:70px
