.uicore-base .uicore-core-setting .uicore-current-color{height:28px;width:100%;display:block;top:0;right:0;bottom:0;position:relative;margin-left:auto;border-radius:3px;border:4px solid var(--uicore-base-input-bg)
}
.uicore-base .uicore-core-setting .uicore-current-color:hover{cursor:pointer;border-color:var(--uicore-base-input-focus-bg)
}
.uicore-base .uicore-core-setting.uicore-split-set{grid-template-columns:1fr;justify-content:start;gap:0
}
.uicore-base .uicore-core-setting small{line-height:24px;margin-right:5px;font-weight:500;letter-spacing:-.2px;font-size:12px
}
.uicore-base .uicore-core-setting input,.uicore-base .uicore-core-setting select,.uicore-base .uicore-core-setting text-area{width:var(--uicore-default-input-width);max-width:var(--uicore-default-input-width)
}
.uicore-base .uicore-core-setting .uicore_small{--uicore-default-input-width:70px
}
.uicore-base .uicore-core-setting .uicore_em:after,.uicore-base .uicore-core-setting .uicore_ms:after,.uicore-base .uicore-core-setting .uicore_pr:after,.uicore-base .uicore-core-setting .uicore_px:after{position:absolute;right:8px;top:0;line-height:28px;pointer-events:none;color:rgba(0,0,0,.25);z-index:1;font-size:13px;font-weight:600
}
.uicore-base .uicore-base_input,.uicore-base input,.uicore-base select,.uicore-base textarea{font-family:Inter,sans-serif;background-color:var(--uicore-base-input-bg);border:none;border-radius:3px;padding:5px 10px;font-size:13px;line-height:18px;color:var(--uicore-base-text-strong);width:100%;box-sizing:border-box;outline:none;transition:all .2s ease-in-out;height:auto;font-weight:500
}
.uicore-base .uicore-base_input:focus,.uicore-base input:focus,.uicore-base select:focus,.uicore-base textarea:focus{background-color:var(--uicore-base-input-focus-bg)
}
.uicore-base{position:fixed;right:30px;bottom:30px;z-index:9999;background:#fff;box-shadow:0 4px 4px hsla(0,0%,68.2%,.25),0 9px 40px -15px hsla(0,0%,68.2%,.25);border-radius:4px;max-height:510px;width:350px;height:95vh;color:#2f2f32;display:flex;flex-direction:column
