.uicore-base .uicore-base_header .uicore-base span.ui-badge{background:var(--uicore-primary,#532df5);border-radius:4.5px;color:#fff;font-size:10px;line-height:13px;font-weight:600;padding:4px 6px 4px 7px;letter-spacing:.4px;margin-left:5px
